#issues-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 25px;
  padding: 30px;
  box-shadow: 1.0431px 1.0431px 15.6466px rgba(0, 0, 0, 0.15);
  border-radius: 9.38793px;
  font-family: "Open Sans";
}

#issues-info p {
  width: 80vw;
}

#issues-info a {
  text-decoration: none;
}

@media (max-width: 1200px) {
  #issues-info-header {
    display: none;
  }
}
